import Taro from '@tarojs/api'
import { createCallbackManager } from '../utils/index'

/**
 * @typedef {object} InnerAudioContext
 * @property {string} src 2.2.3 ID
 * @property {number} [startTime=0] s 0
 * @property {boolean} [autoplay=false]  false
 * @property {boolean} [loop=false]  false
 * @property {boolean} [obeyMuteSwitch=true]  true false  2.3.0  wx.setInnerAudioOption 
 * @property {number} [volume=1]  0~1 1
 * @property {number} duration  s src 
 * @property {number} currentTime  s src  6 
 * @property {boolean} paused 
 * @property {number} buffered 
 * @property {() => void} play() 
 * @property {() => void} pause() 
 * @property {() => void} stop() 
 * @property {(position: number) => void} seek(number position) 
 * @property {() => void} destroy() 
 * @property {(callback: function) => void} offCanplay(function callback) 
 * @property {(callback: function) => void} offEnded(function callback) 
 * @property {(callback: function) => void} offError(function callback) 
 * @property {(callback: function) => void} offPause(function callback) 
 * @property {(callback: function) => void} offPlay(function callback) 
 * @property {(callback: function) => void} offSeeked(function callback) 
 * @property {(callback: function) => void} offSeeking(function callback) 
 * @property {(callback: function) => void} offStop(function callback) 
 * @property {(callback: function) => void} offTimeUpdate(function callback) 
 * @property {(callback: function) => void} offWaiting(function callback) 
 * @property {(callback: function) => void} onCanplay(function callback) 
 * @property {(callback: function) => void} onEnded(function callback) 
 * @property {(callback: function) => void} onError(function callback) 
 * @property {(callback: function) => void} onPause(function callback) 
 * @property {(callback: function) => void} onPlay(function callback) 
 * @property {(callback: function) => void} onSeeked(function callback) 
 * @property {(callback: function) => void} onSeeking(function callback) 
 * @property {(callback: function) => void} onStop(function callback) 
 * @property {(callback: function) => void} onTimeUpdate(function callback) 
 * @property {(callback: function) => void} onWaiting(function callback) 
 */

/**
 *  audio  InnerAudioContext 
 * @returns {InnerAudioContext}
 */
export const createInnerAudioContext = () => {
  /** @type {HTMLAudioElement} */
  let audioEl = new Audio()

  /** @type {InnerAudioContext} */
  const iac = {}

  const callbackManagers = {
    error: createCallbackManager(),
    stop: createCallbackManager()
  }

  iac.play = () => audioEl.play()
  iac.pause = () => audioEl.pause()
  iac.stop = () => {
    iac.pause()
    iac.seek(0)
    callbackManagers.stop.trigger()
  }
  iac.seek = position => {
    audioEl.currentTime = position
  }
  /**
   * @todo destroy
   */
  iac.destroy = () => {
    iac.stop()
    document.body.removeChild(audioEl)
    audioEl = null
  }

  const simpleProperties = ['src', 'autoplay', 'loop', 'volume', 'duration', 'currentTime', 'buffered', 'paused']
  simpleProperties.forEach(propertyName => {
    Object.defineProperty(iac, propertyName, {
      get: () => audioEl[propertyName],
      set (value) { audioEl[propertyName] = value }
    })
  })

  Object.defineProperty(iac, 'startTime', {
    value: 0
  })
  Object.defineProperty(iac, 'obeyMuteSwitch', {
    value: true
  })

  const simpleEvents = [
    'Canplay',
    'Ended',
    'Pause',
    'Play',
    'Seeked',
    'Seeking',
    'TimeUpdate',
    'Waiting'
  ]
  const simpleListenerTuples = [
    ['on', audioEl.addEventListener],
    ['off', audioEl.removeEventListener]
  ]

  simpleEvents.forEach(eventName => {
    simpleListenerTuples.forEach(([eventNamePrefix, listenerFunc]) => {
      Object.defineProperty(iac, `${eventNamePrefix}${eventName}`, {
        get () {
          return callback => listenerFunc.call(audioEl, eventName.toLowerCase(), callback)
        }
      })
    })
  })

  const customEvents = ['Stop', 'Error']
  const customListenerTuples = [
    ['on', 'add'],
    ['off', 'remove']
  ]

  customEvents.forEach(eventName => {
    customListenerTuples.forEach(([eventNamePrefix, actionName]) => {
      Object.defineProperty(iac, `${eventNamePrefix}${eventName}`, {
        get () {
          return callback => callbackManagers[eventName.toLowerCase()][actionName](callback)
        }
      })
    })
  })

  Taro.eventCenter.on('__taroRouterChange', () => { iac.stop() })

  return iac
}
