// const ORIENTATION_MAP = {
//   // up  Exif  1 orientation 
//   1: 'up',

//   // up-mirrored  up Exif  2
//   2: 'up-mirrored',

//   // down 180 Exif  3
//   3: 'down',

//   // down-mirrored  down Exif  4
//   4: 'down-mirrored',

//   // left-mirrored  left Exif  5
//   5: 'left-mirrored',

//   // right 90 Exif  6
//   6: 'right',

//   // right-mirrored  right Exif  7
//   7: 'right-mirrored',

//   // left 90 Exif  8
//   8: 'left'
// }

/**
 * @typedef {'up'|'up-mirrored'|'down'|'down-mirrored'|'left-mirrored'|'right'|'right-mirrored'|'left'} Orientation
 * @typedef ImageInfo object.success 
 * @property {number} width px
 * @property {number} height px
 * @property {string} path 
 * @property {Orientation} orientation 
 * @property {string} type 
 */

/**
 * download
 * @param {Object} object 
 * @param {string} object.src 
 * @param {(res: ImageInfo) => void} [object.success] 
 * @param {function} [object.fail] 
 * @param {function} [object.complete] 
 */
const getImageInfo = ({ src, success, fail, complete } = {}) => {
  return new Promise((resolve, reject) => {
    const onSuccess = res => {
      success && success(res)
      complete && complete()
      resolve(res)
    }
    const onError = res => {
      fail && fail(res)
      complete && complete()
      reject(res)
    }
    const image = new Image()
    image.onload = () => {
      onSuccess({
        errMsg: 'getImageInfo:ok',
        width: image.naturalWidth,
        height: image.naturalHeight
      })
    }
    image.onerror = e => {
      onError({
        errMsg: `getImageInfo:fail ${e.message}`
      })
    }
    image.src = src
  })
}

export default getImageInfo
