import { findDOM } from '../utils/index'

/**
 * @typedef {Object} Param
 * @property {Number} [x]  0
 * @property {Number} [y]  0
 * @property {Number} [width]  canvas-x
 * @property {Number} [height]  canvas-y
 * @property {Number} [destWidth]  width*
 * @property {Number} [destHeight]  height*
 * @property {String} canvasId  <canvas>  canvas-id
 * @property {String} [fileType]  png
 * @property {Number} quality  jpg  (0, 1] 1.0 
 * @property {Function} [success] 
 * @property {Function} [fail] 
 * @property {Function} [complete] 
 */

/**
 *  draw() 
 * @param {Param} object 
 * @todo 
 */
const canvasToTempFilePath = ({ canvasId, fileType, quality, success, fail, complete }, inst) => {
  /** @type {HTMLCanvasElement} */
  const canvas = findDOM(inst).querySelector(`canvas[canvas-id=${canvasId}]`)

  try {
    // /** @type {CanvasRenderingContext2D} */
    const dataURL = canvas.toDataURL(`image/${fileType || 'png'}`, quality)
    const res = {
      tempFilePath: dataURL,
      res: 'canvasToTempFilePath:ok'
    }

    success && success(res)
    complete && complete()
    return Promise.resolve(res)
  } catch (e) {
    const res = {
      errMsg: e.message
    }
    fail && fail(res)
    complete && complete()
    return Promise.reject(res)
  }
}

export default canvasToTempFilePath
