import { findDOM } from '../utils/index'

/**
 * @typedef {Object} Param
 * @property {String} canvasId  <canvas>  canvas-id 
 * @property {Number} x 
 * @property {Number} y 
 * @property {Number} width 
 * @property {Number} height 
 * @property {Function} [success] 
 * @property {Function} [fail] 
 * @property {Function} [complete] 
 */

/**
 *  canvas 
 * @param {Param} object 
 */
const canvasGetImageData = ({ canvasId, success, fail, complete, x, y, width, height }, inst) => {
  /** @type {HTMLCanvasElement} */
  const canvas = findDOM(inst).querySelector(`canvas[canvas-id="${canvasId}"]`)

  try {
    const ctx = canvas.getContext('2d')
    const data = ctx.getImageData(x, y, width, height)
    const res = {
      width,
      height,
      data
    }
    success && success(res)
    complete && complete()
    return Promise.resolve(res)
  } catch (e) {
    const res = {
      errMsg: e.message
    }
    fail && fail(res)
    complete && complete()
    return Promise.reject(res)
  }
}

export default canvasGetImageData
