import { createCallbackManager } from '../utils'
import {
  convertObjectUrlToBlob,
  NETWORK_TIMEOUT,
  setHeader,
  XHR_STATS
} from './utils'

const createUploadTask = ({ url, filePath, formData, name, header, timeout, fileName, success, error }) => {
  let timeoutInter
  let formKey
  const apiName = 'uploadFile'
  const xhr = new XMLHttpRequest()
  const form = new FormData()
  const callbackManager = {
    headersReceived: createCallbackManager(),
    progressUpdate: createCallbackManager()
  }

  xhr.open('POST', url)
  setHeader(xhr, header)

  for (formKey in formData) {
    form.append(formKey, formData[formKey])
  }

  xhr.upload.onprogress = e => {
    const { loaded, total } = e
    callbackManager.progressUpdate.trigger({
      progress: Math.round(loaded / total * 100),
      totalBytesSent: loaded,
      totalBytesExpectedToSent: total
    })
  }

  xhr.onreadystatechange = () => {
    if (xhr.readyState !== XHR_STATS.HEADERS_RECEIVED) return
    callbackManager.headersReceived.trigger({
      header: xhr.getAllResponseHeaders()
    })
  }

  xhr.onload = () => {
    const status = xhr.status
    clearTimeout(timeoutInter)
    success({
      errMsg: `${apiName}:ok`,
      statusCode: status,
      data: xhr.responseText || xhr.response
    })
  }

  xhr.onabort = () => {
    clearTimeout(timeoutInter)
    error({
      errMsg: `${apiName}:fail abort`
    })
  }

  xhr.onerror = e => {
    clearTimeout(timeoutInter)
    error({
      errMsg: `${apiName}:fail ${e.message}`
    })
  }

  /**
   * 
   */
  const abort = () => {
    clearTimeout(timeoutInter)
    xhr.abort()
  }

  const send = () => {
    xhr.send(form)
    timeoutInter = setTimeout(() => {
      xhr.onabort = null
      xhr.onload = null
      xhr.upload.onprogress = null
      xhr.onreadystatechange = null
      xhr.onerror = null
      abort()
      error({
        errMsg: `${apiName}:fail timeout`
      })
    }, timeout || NETWORK_TIMEOUT)
  }

  convertObjectUrlToBlob(filePath)
    .then(fileObj => {
      form.append(name, fileObj, fileName || fileObj.name || `file-${Date.now()}`)
      send()
    })
    .catch(e => {
      error({
        errMsg: `${apiName}:fail ${e.message}`
      })
    })

  /**
   *  HTTP Response Header 
   * @param {HeadersReceivedCallback} callback HTTP Response Header 
   */
  const onHeadersReceived = callbackManager.headersReceived.add
  /**
   *  HTTP Response Header 
   * @param {HeadersReceivedCallback} callback HTTP Response Header 
   */
  const offHeadersReceived = callbackManager.headersReceived.remove

  /**
   * 
   * @param {ProgressUpdateCallback} callback HTTP Response Header 
   */
  const onProgressUpdate = callbackManager.progressUpdate.add
  /**
   * 
   * @param {ProgressUpdateCallback} callback HTTP Response Header 
   */
  const offProgressUpdate = callbackManager.progressUpdate.remove

  return {
    abort,
    onHeadersReceived,
    offHeadersReceived,
    onProgressUpdate,
    offProgressUpdate
  }
}

/**
 *  HTTPS POST  content-type  multipart/form-data
 * @param {Object} object 
 * @param {string} object.url 
 * @param {string} object.filePath 
 * @param {string} object.name  key key 
 * @param {Object} [object.header] HTTP  HeaderHeader  Referer
 * @param {Object} [object.formData] HTTP  form data
 * @param {number} [object.timeout] 
 * @param {string} [object.fileName] 
 * @param {function} [object.success] 
 * @param {function} [object.fail] 
 * @param {function} [object.complete] 
 * @returns {UploadTask}
 */
const uploadFile = ({ url, filePath, name, header, formData, timeout, fileName, success, fail, complete }) => {
  let task
  const promise = new Promise((resolve, reject) => {
    task = createUploadTask({
      url,
      header,
      name,
      filePath,
      formData,
      timeout,
      fileName,
      success: res => {
        success && success(res)
        complete && complete()
        resolve(res)
      },
      error: res => {
        fail && fail(res)
        complete && complete()
        reject(res)
      }
    })
  })

  promise.headersReceive = task.onHeadersReceived
  promise.progress = task.onProgressUpdate
  promise.abort = task.abort

  return promise
}

export default uploadFile
