import { findDOM } from '../utils/index'

/**
 *  canvas  CanvasContext 
 * @param {string} canvasId  <canvas>  canvas-id 
 */
const createCanvasContext = (canvasId, inst) => {
  /** @type {HTMLCanvasElement} */
  const canvas = findDOM(inst).querySelector(`canvas[canvas-id="${canvasId}"]`)

  /** @type {CanvasRenderingContext2D} */
  const ctx = canvas.getContext('2d')

  /**
   * @typedef {Object} Action
   * @property {Function} func
   * @property {any[]} args
   */

  /**
   * 
   * @type {Action[]}
   */
  const actions = []
  const enqueueActions = (func) => {
    return (...args) => {
      actions.push({
        func,
        args
      })
    }
  }
  const emptyActions = () => {
    actions.length = 0
  }

  /**
   *  canvas 
   * @param {Boolean} [reserve=false] 
   *  reserve  false native 
   *  reserve  true drawCanvas 
   *  false
   * @param {Function} [callback] 
   * @todo drawwidthheight
   */
  const draw = async (reserve = false, callback) => {
    try {
      if (!reserve) {
        ctx.clearRect(0, 0, canvas.width, canvas.height)
      }

      //  action 
      for (const { func, args } of actions) {
        await func.apply(ctx, args)
      }

      emptyActions()
      callback && callback()
    } catch (e) {
      /* eslint-disable no-throw-literal */
      throw {
        errMsg: e.message
      }
    }
  }

  const customProperties = [
    /**
     * 
     * @param {String} color  black
     */
    [
      'setFillStyle',
      (color) => {
        ctx.fillStyle = color
      }
    ],
    /**
     * 
     * @param {Number} fontSize 
     */
    [
      'setFontSize',
      (fontSize) => {
        ctx.font = fontSize
      },
      true
    ],
    /**
     * 
     * @param {Number} alpha  0-10 1 
     */
    [
      'setGlobalAlpha',
      (alpha) => {
        ctx.globalAlpha = alpha
      }
    ],
    /**
     * 
     * @param {Number[]} pattern 
     * @param {Number} offset 
     */
    [
      'setLineDash',
      (pattern, offset) => {
        ctx.setLineDash(pattern)
        ctx.lineDashOffset = offset
      }
    ],
    /**
     * ind
     * @param {String} lineCap 
     */
    [
      'setLineCap',
      (lineCap) => {
        ctx.lineCap = lineCap
      }
    ],
    /**
     * 
     * @param {String} lineJoin 
     */
    [
      'setLineJoin',
      (lineJoin) => {
        ctx.lineJoin = lineJoin
      }
    ],
    /**
     * 
     * @param {number} lineWidth px
     */
    [
      'setLineWidth',
      (lineWidth) => {
        ctx.lineWidth = lineWidth
      }
    ],
    /**
     *  CanvasContext.setLineJoin()  miter  lineJoin  bevel 
     * @param {number} miterLimit 
     */
    [
      'setMiterLimit',
      (miterLimit) => {
        ctx.miterLimit = miterLimit
      }
    ],
    /**
     * 
     * @param {number} offsetX  0
     * @param {number} offsetY  0
     * @param {number} blur  0- 100 0
     * @param {string} color  black
     */
    [
      'setShadow',
      (offsetX, offsetY, blur, color) => {
        ctx.shadowOffsetX = offsetX
        ctx.shadowOffsetY = offsetY
        ctx.shadowColor = blur
        ctx.shadowBlur = color
      }
    ],
    /**
     * 
     * @param {String} color  black
     */
    [
      'setStrokeStyle',
      (color) => {
        ctx.strokeStyle = color
      }
    ],
    /**
     * 
     * @param {String} align 
     */
    [
      'setTextAlign',
      (align) => {
        ctx.textAlign = align
      },
      true
    ],
    /**
     * 
     * @param {string} textBaseline 
     */
    [
      'setTextBaseline',
      (textBaseline) => {
        ctx.textBaseline = textBaseline
      },
      true
    ],
    [
      'drawImage',
      (url, ...extra) => {
        //  Image
        if (typeof url === 'string') {
          const img = new Image()
          img.src = url
          return new Promise((resolve, reject) => {
            img.onload = () => {
              ctx.drawImage(img, ...extra)
              resolve()
            }
            img.onerror = reject
          })
        }

        ctx.drawImage(url, ...extra)
      }
    ]
  ]

  const functionProperties = [
    ['arc'],
    ['arcTo'],
    ['beginPath'],
    ['bezierCurveTo'],
    ['clearRect'],
    ['clip'],
    ['closePath'],
    ['createLinearGradient', true],
    ['createPattern', true],
    ['fill'],
    ['fillRect'],
    ['fillText'],
    ['lineTo'],
    ['measureText', true],
    ['moveTo'],
    ['quadraticCurveTo'],
    ['rect'],
    ['restore'],
    ['rotate'],
    ['save'],
    ['scale'],
    ['setTransform'],
    ['stroke'],
    ['strokeRect'],
    ['strokeText'],
    ['transform'],
    ['translate']
  ]

  const valueProperties = [
    ['fillStyle'],
    ['font', true],
    ['globalAlpha'],
    ['lineCap'],
    ['lineDashOffset'],
    ['lineJoin'],
    ['lineWidth'],
    ['miterLimit'],
    ['shadowOffsetX'],
    ['shadowOffsetY'],
    ['shadowColor'],
    ['shadowBlur'],
    ['strokeStyle'],
    ['textAlign', true],
    ['textBaseline', true],
    ['direction', true],
    ['globalCompositeOperation'],
    ['imageSmoothingEnabled '],
    ['imageSmoothingQuality'],
    ['filter']
  ]

  const CanvasContext = { __raw__: ctx }

  customProperties.forEach(([funcName, func, setImmediatly]) => {
    Object.defineProperty(CanvasContext, funcName, {
      get () {
        const fn = enqueueActions(func)

        return (...args) => {
          // 
          if (setImmediatly) {
            func.apply(ctx, args)
          }

          return fn(...args)
        }
      },
      enumerable: true
    })
  })

  functionProperties.forEach(([funcName, isSync]) => {
    Object.defineProperty(CanvasContext, funcName, {
      get: isSync
        ? () => ctx[funcName].bind(ctx)
        : () => enqueueActions(ctx[funcName]),
      enumerable: true
    })
  })

  valueProperties.forEach(([propertyName, setImmediatly]) => {
    Object.defineProperty(CanvasContext, propertyName, {
      get () {
        return ctx[propertyName]
      },
      set (value) {
        if (setImmediatly) {
          ctx[propertyName] = value
        }

        enqueueActions(() => {
          ctx[propertyName] = value
        })()
        return true
      }
    })
  })

  Object.defineProperty(CanvasContext, 'createCircularGradient', {
    /**
     * @param {number} x
     * @param {number} y
     * @param {number} r
     */
    value: (x, y, r) => {
      const radialGradient = ctx.createRadialGradient(x, y, 0, x, y, r)

      return radialGradient
    }
  })

  Object.defineProperty(CanvasContext, 'draw', {
    value: draw
  })

  return CanvasContext
}

export default createCanvasContext
