import { getTimingFunc, easeInOut } from '../utils/index'

/**
 * @typedef {object} PageScrollToParam pageScrollTo
 * @property {number} scrollTop  px
 * @property {string} selector , css selector
 * @property {number} [duration=300]  ms
 * @property {function} [success] 
 * @property {function} [fail] 
 * @property {function} [complete] 
 */

let scrollFunc
let timer
const FRAME_DURATION = 17

/**
 * 
 * @param {PageScrollToParam} object 
 */
export const pageScrollTo = ({ scrollTop, selector, duration = 300, success, fail, complete }) => {
  return new Promise((resolve, reject) => {
    try {
      if (scrollTop === undefined && !selector) {
        throw Error('"scrollTop"  "selector" ')
      }

      let el
      if (document.querySelector('.taro-tabbar__tabbar') === null) {
        // tabbar
        el = window
      } else {
        // tabbar
        el = document.querySelector('.taro-tabbar__panel') || window
      }

      if (!scrollFunc) {
        if (el === window) {
          scrollFunc = pos => {
            if (pos === undefined) {
              return window.pageYOffset
            } else {
              window.scrollTo(0, pos)
            }
          }
        } else {
          scrollFunc = pos => {
            if (pos === undefined) {
              return el.scrollTop
            } else {
              el.scrollTop = pos
            }
          }
        }
      }

      if (scrollTop && selector) {
        console.warn('"scrollTop"  "selector" selector')
      }
      const from = scrollFunc()
      let to
      if (typeof scrollTop === 'number') {
        to = scrollTop
      } else {
        to = document.querySelector(selector).offsetTop
      }
      const delta = to - from

      const frameCnt = duration / FRAME_DURATION
      const easeFunc = getTimingFunc(easeInOut, frameCnt)

      const scroll = (frame = 0) => {
        const dest = from + delta * easeFunc(frame)
        scrollFunc(dest)
        if (frame < frameCnt) {
          timer && clearTimeout(timer)
          timer = setTimeout(() => {
            scroll(frame + 1)
          }, FRAME_DURATION)
        } else {
          const res = {
            errMsg: 'pageScrollTo:ok'
          }
          success && success(res)
          complete && complete()
          resolve(res)
        }
      }
      scroll()
    } catch (e) {
      const res = {
        errMsg: `pageScrollTo:fail ${e.message}`
      }
      fail && fail(res)
      complete && complete()
      reject(res)
    }
  })
}
