import { findDOM } from '../utils/index'

/**
 * @typedef {Object} Param
 * @property {String} canvasId  <canvas>  canvas-id 
 * @property {Uint8ClampedArray} data  rgba
 * @property {Number} x x 
 * @property {Number} y y 
 * @property {Number} width 
 * @property {Number} height 
 * @property {Function} [success] 
 * @property {Function} [fail] 
 * @property {Function} [complete] 
 */

/**
 *  this <canvas> 
 * @param {Param} object 
 * @todo 
 */
const canvasPutImageData = ({ canvasId, data, x, y, success, fail, complete }, inst) => {
  /** @type {HTMLCanvasElement} */
  const canvas = findDOM(inst).querySelector(`canvas[canvas-id="${canvasId}"]`)

  try {
    const ctx = canvas.getContext('2d')

    ctx.putImageData(data, x, y)
    const res = {
      errMsg: 'canvasPutImageData:ok'
    }
    success && success(res)
    complete && complete()
    return Promise.resolve(res)
  } catch (e) {
    const res = {
      errMsg: e.message
    }
    fail && fail(res)
    complete && complete()
    return Promise.reject(res)
  }
}

export default canvasPutImageData
